import { mapActions } from 'vuex';

export default {
	data() {
		return {
			SearchString: '',
			show: false,
			HostSearch: [],
			SearchHistory: [],
			PageIndex: 0,
			PageSize: 10,
			BeginTime: '',
			EndTime: '',
			loading: true,
			list: []
		};
	},
	onLoad() {
		this.$Fetch({
			url: 'm_CreateCourse/getSearchHistoryAndHotSearch',
			success: res => {
				console.log(res);
				this.HostSearch = res.HostSearch;
			}
		});
	},
	onShow() {
		let SearchHistory = uni.getStorageSync('SearchHistory') || [];
		this.SearchHistory = SearchHistory;
	},
	methods: {
		...mapActions(['navFilter']),
		
		onChange(e) {
			this.SearchString = e.detail;
		},
		onClear() {
			this.show = false;
		},
		onSearch(e) {
			let kwd = e.detail.trim();
			let SearchHistory = uni.getStorageSync('SearchHistory') || [];
			console.log(SearchHistory);
			if (SearchHistory.lenght > 12) {
				SearchHistory.pop();
				SearchHistory.unshift(kwd);
			} else {
				SearchHistory.unshift(kwd);
			}
			uni.setStorageSync('SearchHistory', SearchHistory);

			this.show = true;
			this.SearchString = e.detail;
			this.SearchHistory = SearchHistory;
			this.loading = true;
			this.list = [];
			this.PageIndex = 0;
			this.getResultList();
		},
		//点击标签搜索
		clickTag(kwd) {
			let SearchHistory = uni.getStorageSync('SearchHistory') || [];
			if (SearchHistory.lenght > 12) {
				SearchHistory.pop();
				SearchHistory.unshift(kwd);
			} else {
				SearchHistory.unshift(kwd);
			}
			uni.setStorageSync('SearchHistory', SearchHistory);

			this.show = true;
			this.SearchString = kwd;
			this.SearchHistory = SearchHistory;
			this.loading = true;
			this.list = [];
			this.PageIndex = 0;

			this.getResultList();
		},
		//获取搜索结果列表分页
		getResultList() {
			if (this.loading) {
				uni.showLoading({
					title: '加载中...'
				});
				this.loading = false;
				this.$Fetch({
					url: 'm_CreateCourse/getPageForALL',
					data: {
						PageIndex: this.PageIndex,
						PageSize: this.PageSize,
						SearchString: this.SearchString,
						BeginTime: this.BeginTime || '1900-01-01',
						EndTime: this.EndTime || '1900-01-01'
					},
					success: res => {
						console.log(res)
						let arr = res.PagedItems;
						if (arr.length > 0) {
							this.list = this.list.concat(arr);

							if (arr.length === this.PageSize) {
								
								this.PageIndex++;
								this.loading = true;
							}
						}
					}
				});
			}
		},
	}
};
